___CSS_LOADER_EXPORT___.push([module.id, `.main {
    background-color: var(--primary-background-color);

    flex: 1;
    min-height: 100vh;
}

.main__header {
    background-color: var(--primary-background-color-lighter);

    padding: 5px 20px;
    border-bottom: 3px var(--primary-text-color) solid;
    display: flex;
    gap: 10px;
    align-items: center;
}

.main__title {
    font-size: 3rem;
    font-weight: bold;
    flex: 1;
}

#main__task-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-template-rows: 150px;
    gap: 25px;
    padding: 10px;
}

.main__new-task {
    font-size: 1.5rem;
    background-color: inherit;
    color: var(--primary-text-color);
    border: 3px var(--primary-text-color) dashed;
    border-radius: 10px;
    cursor: pointer;

    width: calc(100% - 20px);
    box-sizing: border-box;
    margin: 5px 10px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.main__new-task:hover {
    background-color: var(--primary-background-color-lighter);
}

.main__new-task:active {
    background-color: var(--primary-background-color-darker);
