___CSS_LOADER_EXPORT___.push([module.id, `.dialog-modal {
    min-width: 300px;
}

.dialog-header {
    display: flex;
}

.dialog-title {
    font-size: 2rem;
    flex: 1;
    font-weight: bold;
}

.dialog-modal fieldset {
    margin-top: 15px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    border: 0;
    padding: 0;
}

.dialog-modal fieldset:invalid > label {
    color: var(--error-color);
}

.dialog-modal textarea {
    box-sizing: border-box;
    min-width: 100%;
    min-height: 4rem;
}

.dialog-short-number-input {
    width: 50px;
}

.dialog-side-by-side {
    display: flex;
    gap: 20px;
}

.dialog-side-by-side fieldset {
    margin-top: 0;  /* Fix double margins due to being nested */
