___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
    background-color: var(--accent-background-color);
    color: var(--accent-text-color);

    width: 300px;
    min-height: 100vh;
}

.sidebar__title {
    font-size: 3rem;
    font-weight: bold;
    border-bottom: 3px var(--accent-text-color) solid;

    padding: 5px 20px;
}

.sidebar__list-container {
    margin-top: 10px;
}

.sidebar__new-list, .sidebar__task-list {
    font-size: 1.5rem;
    background-color: inherit;
    border-radius: 10px;
    color: var(--accent-text-color);
    cursor: pointer;

    width: calc(100% - 20px);
    box-sizing: border-box;
    margin: 5px 10px;
    padding: 5px;
}

.sidebar__task-list {
    border: 3px var(--accent-text-color) solid;
}

.sidebar__task-list.current {
    background-color: var(--accent-text-color);
    color: var(--accent-background-color-darker);
    cursor: auto;
}

.sidebar__new-list {
    border: 3px var(--accent-text-color) dashed;
}

.sidebar__new-list:hover, .sidebar__task-list:not(.current):hover {
    background-color: var(--accent-background-color-lighter);
}

.sidebar__new-list:active, .sidebar__task-list:not(.current):active {
    background-color: var(--accent-background-color-darker);
}`, "",{"version":3,"sources":["webpack://./src/style/sidebar.css"],"names":[],"mappings":"AAAA;IACI,gDAAgD;IAChD,+BAA+B;;IAE/B,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,iDAAiD;;IAEjD,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,yBAAyB;IACzB,mBAAmB;IACnB,+BAA+B;IAC/B,eAAe;;IAEf,wBAAwB;IACxB,sBAAsB;IACtB,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,0CAA0C;AAC9C;;AAEA;IACI,0CAA0C;IAC1C,4CAA4C;IAC5C,YAAY;AAChB;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,wDAAwD;AAC5D;;AAEA;IACI,uDAAuD;AAC3D","sourcesContent":[".sidebar {\r\n    background-color: var(--accent-background-color);\r\n    color: var(--accent-text-color);\r\n\r\n    width: 300px;\r\n    min-height: 100vh;\r\n}\r\n\r\n.sidebar__title {\r\n    font-size: 3rem;\r\n    font-weight: bold;\r\n    border-bottom: 3px var(--accent-text-color) solid;\r\n\r\n    padding: 5px 20px;\r\n}\r\n\r\n.sidebar__list-container {\r\n    margin-top: 10px;\r\n}\r\n\r\n.sidebar__new-list, .sidebar__task-list {\r\n    font-size: 1.5rem;\r\n    background-color: inherit;\r\n    border-radius: 10px;\r\n    color: var(--accent-text-color);\r\n    cursor: pointer;\r\n\r\n    width: calc(100% - 20px);\r\n    box-sizing: border-box;\r\n    margin: 5px 10px;\r\n    padding: 5px;\r\n}\r\n\r\n.sidebar__task-list {\r\n    border: 3px var(--accent-text-color) solid;\r\n}\r\n\r\n.sidebar__task-list.current {\r\n    background-color: var(--accent-text-color);\r\n    color: var(--accent-background-color-darker);\r\n    cursor: auto;\r\n}\r\n\r\n.sidebar__new-list {\r\n    border: 3px var(--accent-text-color) dashed;\r\n}\r\n\r\n.sidebar__new-list:hover, .sidebar__task-list:not(.current):hover {\r\n    background-color: var(--accent-background-color-lighter);\r\n}\r\n\r\n.sidebar__new-list:active, .sidebar__task-list:not(.current):active {\r\n    background-color: var(--accent-background-color-darker);\r\n}"],"sourceRoot":""}]);
